@import (reference) "../bootstrap/variables.less";
@import (reference) "../../components/bootstrap/less/mixins.less";
@import (reference) "../mixins.less";
@import (reference) "../utility.less";
@import (reference) "../bootstrap/type.less";

.statement {
    display: flex;
    justify-content: center;
    text-align: center;
    flex-wrap: wrap;
    font-size: 14px;

    p {
        line-height: 17px;
        font-weight: 300;
    }

    &__media {
        width: 100%;
        .fluid-property(margin-bottom, 30, 45);

        img {
            margin: 0 auto;
        }
    }

    &__content {
        width: 100%;
        max-width: 1540px;
        padding: 60px 15px 50px 15px;

        h2 {
            .fluid-property(margin-bottom, 15, 20);
        }
    }

    @media(min-width: @screen-xs-min) {
        &__content {
            width: 60vw;
        }
    }
}

.statement--small {
    .fluid-property(margin-top, 55, 115);

    h3 {
        .fluid-property(font-size, 19, 24);
        margin-bottom: 15px;
    }

    .statement {
        &__content {
            padding: 0;
        }

        &__media {
            .fluid-property(margin-bottom, 25, 35);
        }

        &__spacer {
            .fluid-property(margin-top, 75, 150);
        }
    }
}
